












































	var dateUtils = require('../../common/util.js').dateUtils;
// 	import {
// 		mapState
// 	} from 'vuex'
	import mSearch from '@/components/mehaotian-search-revision/mehaotian-search.vue'

	export default {
		components:{
			mSearch
		},
		data() {
			return {
				userId:'',
				scoreType:1,
				//轮播图配置
				title: 'swiper',
				contentData:[],
				indicatorDots: false,
				autoplay: true,
				interval: 2000,
				duration: 500,
				// 列表
				pageStart:0,
				pageNum:10,
				listData: [],
				reload: false
			}
		},
		// computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
		onLoad() {
			// 			if (!this.hasLogin) {
			// 				uni.showModal({
			// 					title: '未登录',
			// 					content: '您未登录，需要登录后才能继续',
			// 					/**
			// 					 * 如果需要强制登录，不显示取消按钮
			// 					 */
			// 					showCancel: !this.forcedLogin,
			// 					success: (res) => {
			// 						if (res.confirm) {
			// 							/**
			// 							 * 如果需要强制登录，使用reLaunch方式
			// 							 */
			// 							if (this.forcedLogin) {
			// 								uni.reLaunch({
			// 									url: '../login/login'
			// 								});
			// 							} else {
			// 								uni.navigateTo({
			// 									url: '../login/login'
			// 								});
			// 							}
			// 						}
			// 					}
			// 				});
			// 			}
			// 列表
			this.getSwiper();
			this.getList(0);
			this.userId = uni.getStorageSync( 'id');
			this.addLogincore();
		},
		onPullDownRefresh() {
			uni.showNavigationBarLoading();
			uni.showLoading({'title':"加载中..."});
			this.pageStart = 0;
			this.reload = true;
			this.getList(0);
			setTimeout(function() {
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh();
				uni.hideLoading();
			}, 1000)
		},
		onReachBottom() {
			this.getList(1);
		},
		methods: {
			getSwiper(){
				let that = this;
				console.log(this.websiteUrl);
				uni.request({
					url: this.websiteUrl+'uniApp/content/listContent',
					success:(data)=> {
						that.contentData = data.data.data;
					}
				})
			},
			goContentDetail(value){
				console.log(value);
			},
			addLogincore(){
				let that = this;
				if(this.userId != ''){
					uni.request({
						url: this.websiteUrl+'uniApp/score/addUserScore',
						data:{
							userId:that.userId,
							type:that.scoreType
						},
						success:(data)=> {
							if(data.data.data.display = 1){
								uni.showToast({
									title: data.data.data.message,
									duration: 2000
								});
							}
							 ;
						}
					})
				}
				
			},
			//================列表 start =============
			getList(type) {
				let that = this;
				//上拉加载更多
				if( type == 1){
					this.pageStart += 1;
				}else{//下拉刷新或第一次进入
					this.pageStart = 0;
				}
				var data = {
					pageStart:this.pageStart,
					pageNum:this.pageNum
				};
				
				uni.request({
					url: this.websiteUrl+'uniApp/news/getNewsList',
					data: data,
					success: (data) => {
 						if( type == 1){
							that.listData = that.listData.concat(data.data.data.data);
						}else{//下拉刷新或第一次进入
							that.listData = data.data.data.data;
							console.log(that.listData);
						}
						
					},
					fail: (data, code) => {
						uni.stopPullDownRefresh();
						console.log('fail' + JSON.stringify(data));
					}
				})
			},
			goDetail: function(e) {
				// 				if (!/前|刚刚/.test(e.published_at)) {
				// 					e.published_at = dateUtils.format(e.published_at);
				// 				}
				let detail = {
					author: e.author,
					image: e.image,
					id: e.id,
					created: e.created,
					publish_data:e.publish_data,
					title: e.title
				}
				uni.navigateTo({
					url: "../list2detail-detail/list2detail-detail?detailDate=" + encodeURIComponent(JSON.stringify(
						detail))
				})
			},
			setTime: function(items) {
				var newItems = [];
				items.forEach((e) => {
					newItems.push({
						author: e.author,
						image: e.image,
						id: e.id,
						created: dateUtils.format(e.created),
						title: e.title
					});
				});
				return newItems;
			}
		}
		//================列表 end ======================
		
	}
